@import "var.less";

.recommend {
    width: 100%;
    #slide-hold {
        position: relative;
        overflow: hidden;
        width: 100%;
        padding-top: 62.22%;
        #slide {
            position: absolute;
            top: 0;
            left: 0;
            overflow: hidden;
            width: 100%;
            height: 100%;
            ul {
                position: absolute;
                overflow: hidden;
                width: 400%;
                height: 100%;
                li {
                    position: relative;
                    float: left;
                    width: 25%;
                    height: 100%;
                    background: no-repeat center/cover;
                    background-image: url(../img/recommend-slide.png);
                    .content {
                        position: absolute;
                        top: 0;
                        left: 0;
                        padding: 17% 7% 0 8%;
                        .title {
                            font-size: 1.7rem;
                            margin-bottom: 4%;
                        }
                        p {
                            display: -webkit-box;
                            overflow: hidden;
                            width: 80%;
                            height: 4.5rem;
                            margin-top: 4%;
                            text-overflow: ellipsis;
                            -webkit-line-clamp: 3;
                            -webkit-box-orient: vertical;
                        }
                    }
                }
            }
            .dot {
                position: absolute;
                right: 6%;
                bottom: 7%;
                overflow: hidden;
                span {
                    display: block;
                    float: left;
                    width: .6rem;
                    height: .6rem;
                    margin-left: .5rem;
                    border-radius: 50%;
                    background-color: #cfcbc7;
                    &.active {
                        background-color: fade(@pink, 75%);
                    }
                }
            }
        }
    }
    .line {
        width: 95.56%;
        height: 1px;
        margin: 0 auto;
        margin-top: 2rem;
        background: linear-gradient(to right, fade(@yellow, 10%), fade(@yellow, 80%) 3%, fade(@yellow, 80%) 97%, fade(@yellow, 10%));
    }
    .other {
        font-size: 1.2rem;
        line-height: 1;
        position: relative;
        top: -.8rem;
        left: 2.6rem;
        padding: 0 .5rem;
        color: @yellow;
        background-color: #212121;
    }
    ul.acticle {
        overflow: hidden;
        li {
            position: relative;
            float: left;
            width: 46.67%;
            margin-bottom: 2.22%;
            margin-left: 2.22%;
            padding-top: 46.67%;
            background: no-repeat center/cover;
            .content {
                position: absolute;
                bottom: 0;
                width: 100%;
                .title {
                    font-size: 1.3rem;
                    overflow: hidden;
                    margin: .3rem .65rem .4rem;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
                span {
                    display: inline-block;
                    margin-bottom: .3rem;
                    margin-left: .65rem;
                }
            }
        }
    }
}